import React from 'react'
import { AddressBadge } from '../../addressBadge'
import { Component } from '../../../types'

// TODO: depend from '@lidofinance/ui-wallet-modal'
import { WALLET_MODAL, useModal } from '@lidofinance/ui-wallet-modal'

import { WalletCardAccountStyle } from './styles'

export const WalletCardAccount: Component<'div', { account?: string | null }> = (props) => {
  const { account, ...rest } = props
  const { openModal } = useModal(WALLET_MODAL.wallet)

  return (
    <WalletCardAccountStyle {...rest}>
      <AddressBadge address={account} onClick={openModal} color="accent" />
    </WalletCardAccountStyle>
  )
}
